.index-header {
  padding: 100px 0px 0px;
  height: auto;
  min-height: 460px;
  background-image: url(imgs/wedding.jpg);
  background-size: cover;
  background-position: center 0px;
  background-repeat: no-repeat;
  position: relative;
  z-index: 0;
}

.title-row {
  margin: 0px auto 40px;
  padding: 20px 0px 0px;
  min-height: 50px;
  max-width: 1280px;
  position: relative;
  z-index: 2;
}
.title-row > h1 {
  display: block;
  width: 100%;
  text-align: center;
  font-weight: 400;
  color: #40323b;
  margin: 0px 0px 25px;
  /*text-shadow:rgba(255,255,255,01) 1px 1px 5px;*/
}
.title-row > h4 {
  display: block;
  width: 100%;
  text-align: center;
  font-weight: 300;
  font-size: 28px;
  color: #000;
  /*text-shadow:rgba(255,255,255,01) 1px 1px 5px;*/
}
.title-row mark {
  background-color: rgba(255, 255, 255, 0.45);
  border-radius: 5px;
}
.title-row .button-holder {
  padding: 20px 0px 0px;
  display: inline-flex;
  justify-content: center;
}
.title-row .button-holder > a,
.title-row .button-holder > a:link {
  display: inline-block;
  width: auto;
  padding: 10px 35px;
  border-radius: 100px;
  background-color: #991146;
  color: #fff;
  text-decoration: none;
  font-size: 26px;
  font-weight: 100;
  transition: all ease 0.3s;
}
.title-row .button-holder > a:hover {
  background-color: #e63077;
  transition: all ease 0.3s;
}
.header-screen-row {
  margin: 0px auto 0px;
  padding: 60px 0px 120px;
  min-height: 550px;
  max-width: 1280px;
  border-radius: 15px 15px 0px 0px;
  box-sizing: border-box;
  border-top: solid 50px rgba(255, 255, 255, 0.7);
  border-right: solid 10px rgba(255, 255, 255, 0.7);
  border-left: solid 10px rgba(255, 255, 255, 0.7);
  box-shadow: rgba(0, 0, 0, 0.1) 0px -1px 10px;
}
.screen-header-controllers {
  display: inline-block;
  min-width: 60px;
  height: 30px;
  position: absolute;
  margin-top: -100px;
  padding-top: 5px;
}
.screen-header-controllers > div {
  display: inline-block;
  width: 16px;
  height: 16px;
  float: left;
  background-color: rgba(68, 68, 68, 0.3);
  margin: 0px 4px 0px 0px;
  /*box-shadow:rgba(0,0,0,0.3) 0px 1px 2px;*/
  border-radius: 100px;
}
.index-header::after {
  content: "";
  width: 100%;
  height: 280px;
  background-image: url(imgs/header-feather.png);
  background-repeat: repeat-x;
  background-size: 1px 280px;
  position: absolute;
  z-index: 1;
  margin-top: -270px;
}
.backgroundImg {
  width: 360px;
  height: 130px;
  background-image: url(imgs/ew-logo.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin: 0px auto;
}
#exampleModalCenter {
  background-color: #69575ead;
}
.nav-changed {
  background-color: white !important;
}
.container {
  padding: 140px 10px 20px;
  max-width: 1280px;
  width: 100%;
  margin: 0px auto;
}
.btnStart {
  color: #fff;
  background-color: #991146;
}

#planmodal {
  background-color: #69575ead;
}

@import url(https://maxcdn.bootstrapcdn.com/bootstrap/3.3.4/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Lato:400, 300, 900);

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
}

body {
  font-family: "Lato", sans-serif !important;
  font-size: 13pt !important;
  font-weight: 300 !important;
}

.container-fluid {
  padding-right: 0px !important;
  padding-left: 0px !important;
}
#myPageContent,
section {
  height: 100% !important;
  width: 100% !important;
}

/* Slider */
#textSlider.row {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100% !important;
}
#textSlider div {
  color: #e63077;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
}

.iamCol {
  text-align: right;
  float: left;

  font-size: 15pt;
}
.iamCol p:nth-child(2) {
  margin-top: -20pt !important;
}
.slideCol {
  text-align: left;
  overflow: hidden;
  font-weight: 500;
  font-size: 40pt;
  display: block;
  white-space: nowrap;
}
.slideCol p {
  margin: 0px !important;
}

.scroller {
  height: 70pt;
  line-height: 70pt;
  overflow: hidden;
}
.scroller .inner {
  animation: 10s normal infinite running scroll;
}
@keyframes scroll {
  0% {
    margin-top: 0px;
  }
  15% {
    margin-top: 0px;
  }

  25% {
    margin-top: -70pt;
  }
  40% {
    margin-top: -70pt;
  }

  50% {
    margin-top: -140pt;
  }
  65% {
    margin-top: -140pt;
  }

  75% {
    margin-top: -210pt;
  }
  90% {
    margin-top: -210pt;
  }

  100% {
    margin-top: 0px;
  }
}

/*==========  Mobile First Method  ==========*/

/* Custom, iPhone Retina */
@media only screen and (min-width: 320px) and (max-width: 479px) {
  #textSlider.row {
    margin-right: 0px !important;
    margin-left: 0px !important;
  }
  .iamCol {
    text-align: center;
    font-size: 20pt;
  }
  .iamCol p {
    display: inline !important;
  }
  .slideCol {
    font-size: 25pt;
    text-align: center;
    margin-top: -20px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (min-width: 480px) and (max-width: 765px) {
  #textSlider.row {
    margin-right: 0px !important;
    margin-left: 0px !important;
  }
  .iamCol {
    text-align: center;
    font-size: 25pt;
  }
  .iamCol p {
    display: inline !important;
  }
  .slideCol {
    font-size: 38pt;
    text-align: center;
    margin-top: -20px;
  }
}

/* Small Devices, Tablets */
@media only screen and (min-width: 768px) and (max-width: 992px) {
  #textSlider.row {
    margin-right: 0px !important;
    margin-left: 0px !important;
  }
  .iamCol {
    text-align: center;
    font-size: 30pt;
  }
  .iamCol p {
    display: inline !important;
  }
  .slideCol {
    font-size: 50pt;
    text-align: center;
    margin-top: 5px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  #textSlider.row {
    margin-right: 0px !important;
    margin-left: 0px !important;
  }
  .iamCol {
    text-align: right;
    font-size: 30pt;
  }
  .slideCol {
    font-size: 55pt;
    text-align: left;
  }
}
#textSlider {
  position: absolute;
  margin-left: 120px;
}
.btn-primary {
  background-color: #e63077 !important;
  border-color: #e63077 !important;
}
.about-paragraph {
  font-size: 20px !important;
}
