/* Main Navigation */
nav {
  background: #444;
  width: 100%;
}

nav ul {
  padding: 0;
  margin: 0;
}

nav ul li {
  position: relative;
  display: inline-block;
  list-style-type: none;
  margin: 0;
  padding: 10px 0;
}
nav ul li:hover {
  background: #03addf;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  padding: 12px;
}
nav ul li a:hover {
  color: #fff;
  text-decoration: none;
}

.logoutBTN {
  display: block;
  padding: 10px 30px;
  border-radius: 3px;
  font-size: 22px;
  font-weight: 600;
  background-color: rgba(255, 255, 255, 0);
  color: #40323b;
}
.logoutBTN:hover {
  color: #e63077;
}
/* Sub Menu */
nav ul li.parent ul.child {
  display: none; /* Hide Sub Menu */
}
nav ul li.parent:hover ul.child {
  display: block; /* Show Sub Menu on Hover */
  position: absolute;
  top: 40px;
  background: #03addf;
}

nav ul li.parent ul.child li {
  width: 9em;
  padding: 0;
}
nav ul li.parent ul.child li:hover {
  background: #008fba;
}
nav ul li.parent ul.child li a {
  display: block;
  z-index: 3;
}
.child {
  z-index: 3;
}

.headerNav-container {
  width: 100%;
  height: auto;
  padding: 20px 0px 20px;
  /* display:flex;
    justify-content: center; */
  background-color: rgba(101, 70, 82, 0.05);
  border-bottom: solid 1px rgba(255, 255, 255, 0.5);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 10px;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  transition: all ease 0.3s;
}
.headerNav-container > ul {
  display: block;
  width: 100%;
  max-width: 1280px;
  height: auto;
  padding: 0px;
  margin: 0px auto;
  position: relative;
}
.headerNav-container > ul > li {
  float: left;
  list-style-type: none;
  padding: 0px 2px;
  border-right: solid 1px rgba(255, 255, 255, 0.6);
}
.headerNav-container > ul > li:nth-child(7) {
  border-right: none;
  float: right;
  cursor: pointer;
}
.headerNav-container > ul > li:nth-child(9) {
  border-right: none;
  float: right;
  color: #e63077;
  cursor: pointer;
}
.headerNav-container > ul > li:nth-child(8) {
  border-right: none;
  cursor: pointer;
  color: #e63077;
  float: right;
}

.headerNav-container > ul > li:last-child mark {
  border-radius: 50px;
  background-color: #e63077;
  color: white;
}

#cart-nav {
  display: none;
}
.headerNav-container > ul > li > a {
  display: block;
  padding: 10px 30px;
  border-radius: 3px;
  font-size: 22px;
  font-weight: 600;
  background-color: rgba(255, 255, 255, 0);
  text-decoration: none;
  color: #40323b;
  transition: all ease 0.3s;
}
.headerNav-container > ul > li > a:hover {
  color: #fff;
  color: #e63077;
  transition: all ease 0.3s;
}
.headerNav-container > ul > li:nth-child(9) > a:hover {
  background-color: 0;
  font-size: 25px;
}
.sub-menu-holder {
  display: none;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: rgba(65, 50, 60, 0.2) 0px 1px 10px;
  padding: 10px;
  width: 280px;

  transform: translateX(calc(-50% + 70px));
}
.sub-menu-holder > ul {
  padding: 0px;
  margin: 0px;
}
.sub-menu-holder > ul > li {
  list-style-type: none;
}
.sub-menu-holder > ul > li > a {
  display: block;
  padding: 10px 5px;
  text-decoration: none;
  text-align: center;
  font-size: 17px;
  color: rgba(65, 50, 60, 1);
  background-color: rgba(153, 17, 69, 0);
  transition: all ease 0.3s;
}
.sub-menu-holder > ul > li > a:hover {
  color: #fff;
  background-color: rgba(153, 17, 69, 1);
  transition: all ease 0.3s;
}
.headerNav-container > ul > li:hover > .sub-menu-holder {
  display: block;
}
.container {
  padding: 140px 10px 20px;
  max-width: 1280px;
  width: 100%;
  margin: 0px auto;
}
.container.row {
  margin-left: 0px;
  margin-right: 0px;
}

.navlogo {
  background-image: url(../imgs/ew-logo.png);
  width: 120px;
  background-size: contain;
  height: 50px;
  background-repeat: no-repeat;
}
.My-reservation {
  display: none;
}
.logout {
  display: none;
}
